
.progressbar{position:relative;padding:0px
}
.progressbar__p{position:relative;margin:0px;padding:0px;height:100%;background:currentcolor
}
.progressbar__stripes{background-size:30px 30px;background-image:linear-gradient(135deg, rgba(0,0,0,0.1) 25%, transparent 25%, transparent 50%, rgba(0,0,0,0.1) 50%, rgba(0,0,0,0.1) 75%, transparent 75%, transparent);animation:progressbar__a 0.8s linear infinite
}
@keyframes progressbar__a{
from{background-position:0 0
}
to{background-position:60px 30px
}
}



