
html,body{margin:0px;padding:0px;height:100vh
}
body{background:#ffffff
}
#app{min-height:100%
}
.nav-colors{color:white;background:#191919
}
.fade-enter-active,.fade-leave-active{transition:all .2s ease-in-out
}
.fade-enter,.fade-leave-active{opacity:0
}
.slide-enter{transform:translateX(-100%)
}
.slide-enter-active{transition:all .3s ease
}
.slide-leave-active{transition:all .3s ease;transform:translateX(100%)
}



